import React, { useState } from "react";
import iconDelete from "../../../img/iconDelete.png";
import "./Modal.css";
import AddTodo from "../AddTodo";
import data from "../../data";
import { setNotes } from "../../../database";
import { FormLayout, Button } from "@vkontakte/vkui";

export default class ModalDeleteNotes extends React.Component {
  constructor(props) {
    super(props);

    this.state = {
      isActive: false,
    };
  }

  deleteNotes = () => {
    var notesId = data.notesId;
    if (notesId == "1") {
      data.notes1 = "";
    } else if (notesId == "2") {
      data.notes2 = "";
    } else if (notesId == "3") {
      data.notes3 = "";
    } else {
      data.notes4 = "";
    }
    setNotes(
      data.docId,
      data.userId,
      data.notes1,
      data.notes2,
      data.notes3,
      data.notes4
    );
    this.props.setActivePanel("home");
  };

  render() {
    return (
      <React.Fragment>
        <img
          className="btn"
          src={iconDelete}
          onClick={() => this.setState({ isActive: true })}
        />

        {this.state.isActive && (
          <div className="modal">
            <div
              className="modal-field"
              onClick={() => this.setState({ isActive: false })}
            />
            <div className="modal-body">
              <span className="deleteBtns">
                <Button
                  mode="tertiary"
                  size="xl"
                  onClick={() => this.deleteNotes()}
                >
                  Yes
                </Button>
                <Button
                  style={{ marginLeft: 0 }}
                  mode="tertiary"
                  size="xl"
                  onClick={() => this.setState({ isActive: false })}
                >
                  No
                </Button>
              </span>
            </div>
          </div>
        )}
      </React.Fragment>
    );
  }
}
