function addNotes(id, notes1, notes2, notes3, notes4) {
  collection.add({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

function setNotes(docId, id, notes1, notes2, notes3, notes4) {
  collection.doc(docId).set({
    id,
    notes1,
    notes2,
    notes3,
    notes4,
  });
}

